<template>
  <div class="myact">
    <p class="main-title">我的活动</p>
    <ul class="main-type flexBox">
      <li v-for="(item,index) in mainType" :class="index == 0 ? 'act' : ''" v-bind:key="index">
        {{item.name}}（{{item.count}}）
      </li>
    </ul>

    <div class="act-wrap">
      <div class="act-hd">
        <div class="act-hd_lt">
          <div class="act-filter">
            <el-form :inline="true" :model="actFilter" class="demo-form-inline">
              <el-form-item label="筛选条件" class="filterConditions">
                <el-select v-model="actFilter.type" placeholder="活动类别" @change="searchType">
                  <el-option label="砍价" value="1"></el-option>
                  <el-option label="集卡" value="2"></el-option>
                </el-select>
                <el-select v-model="actFilter.state" placeholder="全部" @change="searchStatus">
                  <el-option label="已创建" value="0"></el-option>
                  <el-option label="发布" value="1"></el-option>
                  <el-option label="暂停" value="2"></el-option>
                  <el-option label="结束" value="3"></el-option>
                </el-select>
              </el-form-item>
            </el-form>
          </div>
        </div>
        <div class="act-hd_rt">
          <div class="act-search">
            <el-input placeholder="搜索活动" v-model="actFilter.name">
              <i slot="suffix" class="el-icon-search" @click="search"></i>
            </el-input>
          </div>
          <div>
            <el-button style="margin-left: 10px;" @click="goNew">
              <i class="el-icon-circle-plus" ></i>新建活动</el-button>
          </div>
        </div>
      </div>
      <div class="act-bd">
        <div class="act-table">
          <el-table :data="tableData" border>
            <el-table-column label="活动名称" align="center">
              <template slot-scope="scope">
                <div class="act-title flexBox">
                  <img :src="scope.row.code" alt="" class="icon icon-code" />

                  <img :src="scope.row.img" alt="" class="activity-img" />
                  <div class="name-text flex1">
                    <div class="text">
                      <p class="h1">{{scope.row.title}}</p>
                      <p class="h2">{{scope.row.module}}</p>
                    </div>
                  </div>
                </div>
              </template>
            </el-table-column>
            <el-table-column label="活动时间" align="center" width="180">
              <template slot-scope="scope">
                <div>{{scope.row.startTime}}</div>
                <div>{{scope.row.endTime}}</div>
              </template>
            </el-table-column>
            <el-table-column label="活动状态" align="center" width="140">
              <template slot-scope="scope">
                <div :class="[{'no': scope.row.status == 'create'},{'ing': scope.row.status == 'open'},{'over': scope.row.status == 'finish'}]">{{scope.row.status}}</div>
              </template>
            </el-table-column>
            <el-table-column label="人数限制" align="center" width="100">
              <template slot-scope="scope">
                <div>{{scope.row.limit}}</div>
              </template>
            </el-table-column>
            <el-table-column label="总参与人数" align="center" width="100">
              <template slot-scope="scope">
                <div>{{scope.row.total}}</div>
              </template>
            </el-table-column>
            <el-table-column label="操作" header-align="center" width="190">
              <template slot-scope="scope">
                <el-button type="text" size="small" @click="releaseAct(scope.row)">发布</el-button>
                <el-button type="text" size="small" @click="editAct(scope.row)">编辑</el-button>
                <el-button type="text" size="small" @click="previewAct(scope.row)">预览</el-button>
                <el-button type="text" size="small" @click="dataAct(scope.row)">数据</el-button>
                <div>
                  <el-dropdown @command="more">
                    <span class="el-dropdown-link">
                      更多
                      <i class="el-icon-arrow-down el-icon--right"></i>
                    </span>
                    <el-dropdown-menu id="udp" slot="dropdown">
                      <el-dropdown-item :command="scope.row.actid+'1'">复制</el-dropdown-item>
                      <el-dropdown-item :command="scope.row.actid+'2'">删除</el-dropdown-item>
                      <el-dropdown-item>推广</el-dropdown-item>
                      <el-dropdown-item :command="scope.row">渠道管理</el-dropdown-item>
                    </el-dropdown-menu>
                  </el-dropdown>
                </div>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <div class="act-ft">
        <div class="act-table_pagination">
          <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-sizes="[10, 20, 30, 40]" :page-size="10" layout="total, sizes, prev, pager, next, jumper" :total="listData.count">
          </el-pagination>
        </div>
      </div>
    </div>

    <!-- modal可以删除，使用preview和sources组件 -->
    <modal :contentType="modal.content" 
    v-if="modal.show" 
    v-on:toggleModal="toggleModal" 
    class="perviewModal"
    style="z-index: 100">
      <div class="m-nav">
        <span :class="{'act': curModalTab == 'preview'}" @click="curModalTab = 'preview'">活动预览</span>
        <span :class="{'act': curModalTab == 'manage'}" @click="curModalTab = 'manage'">渠道管理</span>
      </div>
      <div class="m-content">
        <div v-show="curModalTab == 'preview'" class="preview flexBox">
          <preview-phone :userActivityId="indexAct.actid || 10000"></preview-phone>
          <div class="preview-data">
            <div class="img-box flexBox">
              <img :src="indexAct.img" alt="" />
              <div class="flex1">
                <div class="title">
                  <span class="title-tag">砍价</span>
                  <span class="title-text">{{indexAct.activityName}}</span>
                </div>
                <p class="des">{{indexAct.description}}</p>
                <div class="time">
                  <span class="val">
                    <i class="el-icon-time"></i>创建时间：{{indexAct.startTime}}</span>
                  <span class="edit" @click="toEdit">去编辑
                    <span class="el-icon-edit-outline"></span>
                  </span>
                </div>
              </div>
            </div>
            <div class="preview-share">
              <div class="share-code">
                <img src="/static/imgs/index/wechat-code.png" alt="">
                <div class="share-code_label">扫一扫分享到微信</div>
                <div class="down-code">
                  <i class="el-icon-download"></i>下载二维码</div>
              </div>
              <div class="share-otherWay">
                <div class="share-otherWay_title">分享到：</div>
                <ul class="other-way">
                  <li>
                    <i class="el-icon-edit-outline"></i>QQ</li>
                  <li>
                    <i class="el-icon-edit-outline"></i>QQ空间</li>
                  <li>
                    <i class="el-icon-edit-outline"></i>微博</li>
                </ul>
                <div class="link-copy-wrap">
                  <el-input placeholder="请输入内容" value="http://baidu.com">
                    <template slot="append">复制链接</template>
                  </el-input>
                </div>
                <div class="preview-share_describe">您可以将活动链接复制到您的公众号菜单或自定义回复中。</div>
              </div>
            </div>
            <div class="spread">
              <i class="el-icon-edit-outline"></i>
              我要推广
            </div>
          </div>
        </div>
        <div v-show="curModalTab == 'manage'" class="manage">
          <div class="label">哪个渠道客户/流量最多？设置不同渠道分享链接你就知道</div>
          <div class="spreadWay">
            <div class="default">
              <div style="font-size:16px;color: #5A5A5A">默认渠道分享：</div>
              <div class="share-code">
                <img src="/static/imgs/index/wechat-code.png" alt="">
                <div class="share-code_label" style="color: #5A5A5A">扫一扫分享到微信
                  <i class="el-icon-download"></i>
                </div>
                <!-- <div class="down-code">
                  <i class="el-icon-download"></i>下载二维码</div> -->
              </div>
              <div class="link-copy-wrap">
                <el-input placeholder="请输入内容" value="http://baidu.com" class="link-copy-input">
                  <template slot="append">复制链接</template>
                </el-input>
              </div>
              <div class="preview-share_describe">您可以将活动链接复制到您的公众号菜单，或自定义回复中</div>
            </div>
            <div class="other">
              <div style="font-size: 16px;color: #5A5A5A">其他渠道分享：</div>
              <div class="other-describe">
                <span class="other-describe_text">您可将连接（或二维码）放置在微信公众号、或者户外广告等不同渠道，供用户访问。结果统计中可区分用户来自哪个渠道</span>
                <el-button @click="createChannel('默认值')">
                  <i class="el-icon-plus"></i>添加渠道</el-button>
              </div>
              <div class="spread-way-table">
                <el-table :data="spreadWayTable"  style="width: 100%" border max-height="250">
                  <el-table-column label="渠道名称" type="index" prop="name" width="250" align="center">
                    <template slot-scope="scope">
                      <div>
                        <el-input :class="{'editState' : editState.nameState == false}" 
                        v-model="scope.row.channel" 
                        :placeholder="editState.nameState ? '未填写' : '最多20字'" 
                        :disabled="editState.nameState" 
                        style="width: 72%;float: left;">
                        </el-input>
                        <el-button type="text" v-show="editState.nameState" 
                        @click.prevent="editState.nameState = !editState.nameState"
                        style="margin-left: 10px">
                          修改
                        </el-button>

                        <el-button v-show="!editState.nameState" 
                        @click.prevent="editChannel(scope.row)" 
                        size="small" 
                        style="float: right;margin-top: 5px"
                        >
                          保存
                        </el-button>
                      </div>
                    </template>
                  </el-table-column>
                  <el-table-column label="链接" align="center">
                    <template slot-scope="scope">
                      <div>
                        <span>{{scope.row.linkUrl}}</span>
                        <el-button type="text">复制</el-button>
                      </div>
                    </template>
                  </el-table-column>
                  <el-table-column label="二维码" align="center" width="80">
                    <template slot-scope="scope">
                      <div class="spread-code">
                        <img :src="scope.row.urlImg" alt="">
                        <i class="el-icon-download"></i>
                      </div>
                    </template>
                  </el-table-column>
                  <el-table-column label="操作" align="center" width="100">
                    <template slot-scope="scope">
                      <el-button type="text" size="small" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </div>
            </div>
          </div>
          <div class="spread">
            <i class="el-icon-edit-outline"></i>我要推广</div>
        </div>
      </div>
    </modal>

    <!-- 预览组件和渠道管理组件的使用demo -->
    <dialog-sources actid="1" v-if="false"></dialog-sources>
    <dialog-preview actid="1" v-show="false"></dialog-preview>
  </div>
</template>

<script>
import Activity from 'src/server/activity.js'
import utils from 'src/lib/utils.js'
import User from 'src/server/user.js'
import { Message } from 'element-ui'

import DialogSources from '../dialog/sources.vue'
import DialogPreview from '../dialog/preview.vue'
import previewPhone from './preview/preview-phone.vue'

export default {
  components: {
    DialogSources,
    DialogPreview,
    previewPhone
  },
  data () {
    return {
      editState: {
        // true为显示状态，false为编辑状态
        nameState: true
      },
      mainType: [
        { name: '营销互动', id: '', count: 3 },
        { name: '微图文', id: '', count: 0 },
        { name: '微传单', id: '', count: 0 }
      ],
      listData: null,
      indexAct: {},
      lists: [
        {
          code: '../static/imgs/temp/图层 13@2x.png',
          img: '../static/imgs/temp/图层 14@2x.png',
          h1: '这里是活动名称',
          h2: '全民砍价模块',
          start: '2018-01-27 23:19:00',
          end: '2018-02-03 23:19:00',
          state: 0,
          limit: 100,
          total: 200
        }
      ],
      modal: {
        show: false,
        content: 'my-activities'
      },
      curModalTab: 'preview', // 当前modal中的tab
      actFilter: {
        type: '',
        state: '',
        name: null
      },
      curAct: {}, // 当前操作的活动
      // 表格数据
      tableData: [],
      tempRow: null,
      channelList: null,
      // 当前表格页码
      currentPage: 1,
      actDetailDialog: false, // 订单详情对话框的状态
      spreadWayTable: []
    }
  },
  methods: {
    /*    count()
    {
      User.mine().then((res)=>{
        res.rs.count
      }
    } */
    goNew() {
      this.$router.push({
        path: '/nav-new'
      })
    },
    handleDelete(index, row) {
      //删除当前选中行数据     
      let params = {
        id : row.channelID
      }
      console.log("Delete: " + row.channelID)
      User.channelDelete({
        data : params
      }).then(res => {
        if (res.suc && res.code === 200) {
          this.spreadWayTable.splice(index, 1)
        }
      })
      
    },
    searchType (val) {
      // 根据活动类别筛选
      let params = {
        innerType: val
      }

      this.getList(params)
    },
    searchStatus (val) {
      // 根据活动状态筛选
      let params = {
        status: val
      }

      this.getList(params)
    },
    search () {
      // 搜索
      let params = {
        keyword: this.actFilter.name
      }

      this.getList(params)
    },

    toggleModal () {
      this.modal.show = !this.modal.show
    },
    more (command) {
      if ( typeof command == 'object') {
        //渠道管理
        this.indexAct = command
        let params = {
          activityId : command.actid
        }
        this.curModalTab = 'manage'
        this.modal.show = true
        User.query({
          data : params
        }).then(res => {
          if (res.suc && res.code === 200) {
            this.spreadWayTable = res.rs
            this.spreadWayTable.forEach(function (item) {
              item.channelID = item.id

              item.nameState = true
            })
          }
        })
      } else {
        let type = command.substring(command.length-1, command.length)
        let id = command.substring(0,command.length-1)
        console.log(id)
        if (type == '1') {
          //复制活动
          
          Activity.copy({
              data: { id: id }
            }).then(res => {
              if (res.suc) {
                Message({ type: 'success', message: '复制成功' })
                this.getList()
              }
            }) 
        }
        if (type == '2') {
          //删除活动
          Activity.delete({
              data: { id: id }
            }).then(res => {
              if (res.suc) {
                Message({ type: 'success', message: '删除成功' })
                this.getList()
              }
            })
        }
      }
      
    },
    createChannel (val) {
      // 添加渠道
      let params = {
        channelName: val,
        userActivityId: this.indexAct.actid
      }

      let act = this.indexAct
      User.channelCreate({
        data: params
      }).then(res => {
        return User.query({
          data : {activityId : act.actid}
        })
      }).then(res => {
        if (res.suc && res.code === 200) {
            this.spreadWayTable = res.rs
            this.spreadWayTable.forEach(function (item) {
              item.channelID = item.id
              item.nameState = true
          })
        }
        console.log(this.spreadWayTable)
      })
    },
    editMo (val) {
      this.$set(val, 'nameState', false)
      console.log(val)
    },
    editChannel (row) {
      // 编辑渠道
      let params = {
        id: row.channelID,
        channelName: row.channel
      }
      User.channelEdit({
        data: params
      })
      this.editState.nameState = true
    },
    releaseAct (act) {
      // 发布
      let params = {
        id: act.actid
      }
      Activity.publish({
        data: params
      }).then(res => {
        if(res.suc) {
          Message({ type: 'success', message: '保存成功' })
          console.log("DONE")
        }
        else
          console.log("GG")
      })
      console.log(act.actid)
      this.curAct = act
    },
    previewAct (act) {
      // 预览
      this.indexAct = act
      console.log("indexAct" + act.id)
      let params = {
        activityId : act.actid
      }
      /*console.log("activityId:" + this.act.actid)*/
      this.curAct = act
      this.modal.show = true
      User.query({
        data : params
      }).then(res => {
        if (res.suc && res.code === 200) {
          this.spreadWayTable = res.rs
          this.spreadWayTable.forEach(function (item) {
            item.channelID = item.id

            item.nameState = true
          })
        }
      })
    },
    toEdit () {
      console.log("toEdit" + this.indexAct)
      this.editAct(this.indexAct)
    },
    editAct (act) {
      // 编辑
      console.log("act:::" + act.id)
      console.log("asdas:::" + this.indexAct.id)
      this.curAct = act
      // 跳转到编辑页面
      if (act.id) {
        this.$router.push({
          name: 'collect',
          params: {
            id: act.id
          }
        })
      }
    },
    dataAct (act) {
      // 数据
      console.log(act)
      this.curAct = act
    },
    handleSizeChange (val) {
      // 改变每页显示的条数
      console.log(`每页 ${val} 条`)
      let params = {
        pageSize: val
      }
      this.getList(params)
    },
    handleCurrentChange (val) {
      // 页码调整
      let params = {
        page: val
      }
      this.getList(params)

      console.log(`当前页: ${val}`)
    },
    formatState (state) {
      const actStateMap = {
        create: {
          type: 'create',
          label: '未开始'
        },
        open: {
          type: 'open',
          label: '进行中'
        },
        finish: {
          type: 'finish',
          label: '已结束'
        }
      }
      return actStateMap[state] ? actStateMap[state].label : '未开始'
    },
    getList (params) {
       // 获取用户的活动列表
       params = params || null
      Activity.getList({
        data: params
      }).then(res => {
        console.log(res)
        this.listData = res.rs
        this.tableData = res.rs.pageList
        this.tableData.forEach(function (item) {
          item.title = item.activityName
          item.img = item.mainImg
          item.code = item.shareUrl
          item.actid = item.id
          if (item.status === 0 || item.status === 4) {
            item.status = '已创建'
          }
          if (item.status === 1) item.status = '发布'
          if (item.status === 2) item.status = '暂停'
          if (item.status === 3) item.status = '结束'
          if (item.status === 4) item.status = '预发布'
          item.startTime = utils.formatDate(new Date(item.beginTime))
          item.endTime = utils.formatDate(new Date(item.endTime))
          item.module = item.modName
          item.limit = item.goodsCount
          item.total = item.joinUserNum
        })
       
      })
    }
  },
  created () {
    this.$emit('leftAct', 2)
    this.modal.show = false
  },
  mounted () {
    this.getList()
  }
}
</script>

<style lang="scss" scoped src="common/style/module/manage/my.scss">

</style>

<style lang="scss">
.link-copy-wrap {
  .link-copy-input {
    margin-top: 34px;
  }
}
.link-copy-input .el-input .el-input-group .el-input-group--append{
 
}
.el-input-group__append, .el-input-group__prepend {
    cursor: pointer;
    color: #fff;
    background: #fc4a46 !important;
  }

.perviewModal {
  .my-activities {
    height: auto;
  }
}
.el-table th {
  background-color: #F6F6F6;
}
.el-input,
.el-textarea {
  width: initial;
}
.el-textarea {
  width: 80%;
}
.el-input__inner {
  //border: 1px solid #999;
}
.el-input.is-disabled .el-input__inner,
.el-textarea.is-disabled .el-textarea__inner {
 // background: transparent !important;
  border: none;
  cursor: text;
}

.act-hd,
.act-bd,
.act-ft {
  margin-top: 10px;
  cursor: pointer;
}
.act-hd {
  display: flex;
  justify-content: space-between;
  margin-top: 46px;
  .filterConditions {
    margin-left: 38px;
    margin-bottom: 15px;
  }
}
.act-hd_rt {
  display: flex;
  justify-content: space-between;
  margin-right: 31px;
  .el-icon-circle-plus {
    vertical-align: text-bottom;
    font-size: 1.2em;
    margin-right: 0.5em;
  }
  .el-input__suffix {
    top: 10px;
  }
}
.act-table {
  margin-left: 38px;
  margin-right: 34px;
}
.act-table_pagination,
.act-handle {
  margin-bottom: 10px;
  margin-right: 28px;
  display: flex;
  justify-content: flex-end;
}
.myact {
  .el-button--default {
    color: #fff;
    background-color: #fc4a46;
    bact-color: #fc4a46;
  }
  .el-button--text {
    color: #fc4a46;
  }
  .el-dropdown {
    font-size: 12px;
    color: #fc4a46;
    cursor: pointer;
  }
}
.is-center {
  width: 35%;
}
.act-wrap {
  background-color: #ffffff;
  border: 1px #d9d9d9 solid;
  margin-top: 33px;
  border-radius: 6px;
  .no {
    color: #979797;
  }
  .ing {
    color: #7bb95b;
  }
  .over {
    color: #fc4a46;
  }
  .act-title {
    padding: 16px 0 16px 53px;
    position: relative;
    .icon-code {
      width: 20px;
      height: 20px;
      left: 26px;
    }
    /* img.codeAddress {
      width: 20px;
      height: 20px;
      left: 26px;
    } */
    img.activity-img {
      width: 108px;
      height: 76px;
    }

    .name-text {
      position: relative;

      .text {
        position: absolute;

        text-align: center;
        top: 50%;
        transform: translateY(-50%);
        font-size: 14px;
        .h1 {
          color: #161616;
          width: 100px;
          margin: auto;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
        .h2 {
          color: #5a5a5a;
        }
      }
    }
  }
}
</style>


